import { useState } from "react";

const Button = ({ number, symbol }) => <div>{number || symbol}</div>;

const Calculator = () => {
  const [display, setDisplay] = useState("0");

  return (
    <>
      <h2>Calculator</h2>

      <div id="container">
        <div id="display">{display}</div>
        <div id="top-col">
          <Button symbol="C" />
          <Button symbol="()" />
          <Button symbol="%" />
          <Button symbol="/" />
        </div>
        <div id="sec-col">
          <Button number={7} />
          <Button number={8} />
          <Button symbol="C" />
          <Button symbol="C" />
        </div>
        <div id="thi-col">
          <div>4</div>
          <div>5</div>
          <div>6</div>
          <div>-</div>
        </div>
        <div id="fou-col">
          <div>1</div>
          <div>2</div>
          <div>3</div>
          <div>+</div>
        </div>
        <div id="bot-col">
          <div>+/-</div>
          <div>0</div>
          <div>.</div>
          <div>=</div>
        </div>
      </div>

      <style jsx>{`
        #container {
          box-sizing: border-box;
          max-width: 600px;
          height: 750px;
          margin: 0 auto;
          border: 1px solid black;
          display: grid;
          grid-template-columns: repeat(4, 1fr);
          grid-template-rows: repeat(7, 1fr);
        }
        #display {
          width: 100%;
          grid-column: 1 / 5;
          grid-row: 1 / 3;
          background-color: red;
          display: flex;
          justify-content: flex-end;
          align-items: center;
          font-size: 5rem;
        }
        #top-col,
        #sec-col,
        #thi-col,
        #fou-col,
        #fiv-col,
        #bot-col {
          grid-column: 1 / 5;
          display: flex;
          width: 100%;
          justify-content: space-evenly;
          align-items: center;
          font-size: 4.5rem;
        }
        div {
          border: 1px solid grey;
          width: 100%;
          height: 100%;
          display: flex;
          justify-content: center;
          align-items: center;
        }
      `}</style>
    </>
  );
};

export default Calculator;
